         .then((result) => {\r\n                        loading && loading.close();\r\n                        if (result && result.IsSuccess) {\r\n                            that.$message({\r\n                                message: \"保存成功！\",\r\n                                type: \"success\"\r\n                            });\r\n                        } else {\r\n                            that.$confirm(result.ErrorMsg, \"保存失败\", {\r\n                                confirmButtonText: \"确定\",\r\n                                type: \"warning\",\r\n                                center: true,\r\n                                showCancelButton: false\r\n                            });\r\n                        }\r\n                    }).catch((err) => {\r\n                        that.$message({\r\n                            type: 'error',\r\n                            message: err\r\n                        });\r\n                        loading && loading.close();\r\n                    });\r\n            });\r\n        }\r\n    }\r\n}\r\n</script>\r\n<style scoped>\r\n#tips {\r\n  margin: 0 auto;\r\n  height: 200px;\r\n  width: 450px;\r\n  margin-top: 30px;\r\n}\r\n</style>\r\n\r\n\r\n"],"sourceRoot":""}]);

