<template>
    <glui-row>
        <glui-col :span="10">
            <glui-form ref="form" :model="form" label-width="120px">
                <glui-form-item label="产品编号" prop="PBIOldProductId">
                    <glui-input v-model="form.PBIOldProductId"></glui-input>
                </glui-form-item>
                <glui-form-item label="经营方式">
                    <glui-select v-model="form.PBIProductOperateType" prop="PBIProductOperateType" placeholder="请选择经营方式">
                        <glui-option v-for="item in operatetypes" :label="item.name" :value="item.id" :key="item.id">
                        </glui-option>
                    </glui-select>
                </glui-form-item>
                <glui-form-item v-if="form.PBIProductOperateType==2" label="OEM供应商" prop="PSILocalAgencySupplierId">
                    <dropdownsupplier v-model="form.PSILocalAgencySupplierId" :belong="3">
                    </dropdownsupplier>
                </glui-form-item>
                <glui-form-item>
                    <glui-button type="primary" @click="onSubmit">保存</glui-button>
                </glui-form-item>
            </glui-form>
        </glui-col>
        <glui-col :span="10">
            <div id="tips">
                <glui-card class="box-card">
                    <div class="text item">
                        1、此功能为临时OEM供应商调整的入口，请谨慎使用。
                    </div>
                    <div class="text item">
                        2、请联系江蓉统一处理。
                    </div>
                    <div class="text item">
                        3、该功能会在一段时间后下线。
                    </div>
                    <div class="text item">
                        4、若已产生订单和财务单据，请告知订单（李久建）进行调整。
                    </div>
                </glui-card>
            </div>
        </glui-col>
    </glui-row>
</template>
<script>
import enums from '@/js/enums'
import httputils from '@/js/httputils'
import dropdownsupplier from '@/components/dropdownsupplier'
export default {
    components: { dropdownsupplier },
    data() {
        return {
            belong: 3,
            operatetypes: [],
            form: {
                PBIOldProductId: "",
                PBIProductOperateType: "",
                PSILocalAgencySupplierId: "",
                PSILocalAgencySupplierName: ""
            },
            rules: {
                PBIOldProductId: [
                    { required: true, message: '请输入产品编号', trigger: 'blur' }
                ],
                PBIProductOperateType: [
                    { required: true, message: '请选择经营方式', trigger: 'change' }
                ],
                PSILocalAgencySupplierId: [
                    { required: true, message: '请选择OEM供应商', trigger: 'change' }
                ]
            }
        }
    },
    mounted() {
        this.operatetypes = enums.getProductOperateType(this.belong);
    },
    methods: {
        /**
         * 保存提交
         */
        onSubmit() {
            let that = this;
            this.$refs["form"].validate(vali => {
                if (!vali) {
                    return;
                }
                let loading = that.$loading({ lock: true });
                httputils.postjson('/travel/supergoproduct/dsfproxy/postproduct/productbaseinfo/ModifyAgencySupplier', this.form)
                    .then((result) => {
                        loading && loading.close();
                        if (result && result.IsSuccess) {
                            that.$message({
                                message: "保存成功！",
                                type: "success"
                            });
                        } else {
                            that.$confirm(result.ErrorMsg, "保存失败", {
                                confirmButtonText: "确定",
                                type: "warning",
                                center: true,
                                showCancelButton: false
                            });
                        }
                    }).catch((err) => {
                        that.$message({
                            type: 'error',
                            message: err
                        });
                        loading && loading.close();
                    });
            });
        }
    }
}
</script>
<style scoped>
#tips {
  margin: 0 auto;
  height: 200px;
  width: 450px;
  margin-top: 30px;
}
</style>





